.task-checkbox:checked::before {
  transform: scale(1);
}

.task-checkbox:focus-visible {
  outline: max(2px, 0.1em) solid currentColor;
  outline-offset: max(2px, 0.15em);
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: start;
  column-gap: var(--text-deleteSVG-spacing);
}

.delete-svg-wrapper {
  width: var(--deleteSVG-width);
  height: calc(var(--taskInput-lh) * var(--taskInput-fs));
  flex-shrink: 0;
  position: relative;
  opacity: 0;
  cursor: pointer;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: opacity 150ms ease-in-out;
}

.task-wrapper:hover .delete-svg-wrapper {
  opacity: 1;
}

.task-input {
  font-size: var(--taskInput-fs);
  border: none;
  min-width: 0px;
  flex: 1;
  resize: none;
  overflow: hidden;
  line-height: var(--taskInput-lh);
  text-shadow: 0.01em 0.01em 0.15em hsla(0, 100%, 61%, 0.4);
  padding-top: 1px;
  will-change: height;
  width: fit-content;
}

.delete-svg-wrapper:hover {
  animation: blink 150ms infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.task-input:focus,
.task-input:active {
  outline: none;
}

:is(textarea, div).task-notes {
  line-height: 1.5;
  resize: none;
  overflow: hidden;
  outline: none;
  color: hsl(0, 0%, 80%, 90%);
  text-shadow: 0px 0px 1px hsl(0, 0%, 85%);
  border: none;
  padding-right: calc(var(--text-deleteSVG-spacing) + var(--deleteSVG-width));
}

:is(textarea, div).task-notes::placeholder {
  color: inherit;
}

.date-wrapper {
  width: max-content;
}

input[type="date"] {
  border-radius: 0px;
}

.task-content.active .expandable-content-wrapper {
  grid-template-rows: 1fr;
}

.expandable-content-wrapper {
  grid-column: 2;
  display: grid;
  grid-template-rows: 0fr;
  transition: grid-template-rows 200ms;
}

.expandable-content {
  display: grid;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition: opacity 300ms ease-in-out;
  padding-top: 5px;
}

.expandable-content * {
  font-size: 1rem;
}

.task-content.active .expandable-content {
  opacity: 1;
  visibility: visible;
  row-gap: 15px;
}

.more-options {
  display: flex;
  gap: 0.5rem;
}

.more-options > div {
  display: grid;
}

.more-options > div > * {
  background-color: var(--more-options-bg);
}

.task-date:hover,
.task-date.focused,
.priority-btn-wrapper:hover .task-priority,
.priority-btn-wrapper.focused .task-priority {
  background-color: hsl(0, 65%, 20%);
  outline: none;
  border: none;
}

