  padding: 0.7em 1.5em 0.6em;
  line-height: 1;
}

.inner-cyberpunk-clip-wrapper-br.task-priority,
.cyberpunk-clip-wrapper-br.priority-btn-wrapper,
.inner-cyberpunk-clip-wrapper-br.task-date,
