.section-wrapper {
  background-color: rgb(13, 13, 23);
  position: relative;
  padding: 0.5em;
}

.section-wrapper::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: hsl(184, 60%, 40%);
  box-shadow: 0px 0px 1.5px rgb(44, 32, 99);
}

.title-wrapper {
  display: flex;
  justify-content: space-between;
}

.section-title {
  color: rgb(195, 187, 190);
  font-size: 1.5rem;
  line-height: 1;
  height: min-content;
  vertical-align: middle;
  padding-top: 2px;
}

.section-wrapper:not(:first-of-type):has() {
  margin-top: 16px;
}

.expand-collapse {
  background-color: transparent;
  border-radius: 0px;
  color: hsla(337, 6%, 75%, 75%);
  border: none;
  position: relative;
  transition: color 100ms ease-in-out, transform 100ms ease-in-out;
  font-family: "Rajdhani Light";
  line-height: 1;
  padding-top: 2px;
  font-size: 0.9125rem;
}

.expand-collapse:hover {
  color: var(--cyber-blue);
}

