___CSS_LOADER_EXPORT___.push([module.id, `.hamburger {
  display: grid;
  place-items: center;
  width: var(--header-height);
  height: var(--header-height);
  background-color: var(--main-bg);
  border: 1px solid rgb(255, 97, 89);
  z-index: 1;
}

.ham-line-wrapper {
  display: grid;
  gap: 5px;
  width: 75%;
  padding: 5px;
}

.ham-line {
  height: 2.5px;
  background-color: var(--cyber-red);
  width: 100%;
  transition: background-color 200ms ease-in-out;
  box-shadow: 0 0 1.5px rgb(255, 97, 89);
  background-color: rgb(255, 97, 89);
}
