___CSS_LOADER_EXPORT___.push([module.id, `aside {
  grid-row: 2;
  grid-column: 1;
  transform: translateX(-100%);
  --list-gap: 1rem;
  height: 100%;
  overflow-x: hidden;
  padding: var(--header-content-spacing) 1rem 2rem;
  position: relative;
  display: grid;
  grid-auto-rows: min-content;
  gap: 1rem;
}

aside :where(label, .inner-system-list-wrapper, .inner-mylist-wrapper) {
  padding: 0px 0.5rem;
}

.inner-system-list-wrapper,
.inner-mylist-wrapper,
.system-list-wrapper,
.mylist-wrapper {
  display: grid;
  gap: var(--list-gap);
}

.mylist-wrapper {
  grid-auto-rows: min-content;
}

.inner-system-list-wrapper {
  margin-bottom: var(--list-gap);
}

.side-panel.mobile {
  position: fixed;
  top: 0;
  left: 0;
  width: 65%;
  transform: translateX(-100%);
  z-index: 999;
  height: 100%;
  background: linear-gradient(
      to right,
      hsla(353, 79%, 50%, 0.15) 10%,
      var(--main-bg)
    ),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-color: rgb(49, 7, 12);
}

.sidebar.no-transition {
  transition: none;
}

.side-panel.mobile.open {
  transform: translateX(0);
}

.side-panel.desktop {
  position: static;
  width: 17rem;
  transform: translateX(0);
}

.side-panel.desktop.hidden {
  transform: translateX(-100%);
}
