___CSS_LOADER_EXPORT___.push([module.id, `.task-collection-wrapper {
  grid-row: 2;
  grid-column: 1/-1;
  position: relative;
  --task-collection-padding: 16px;
  overflow-y: scroll;
  display: grid;
  grid-template-rows: min-content auto;

  --barcode-height: 18px;
  --barcode-bottom-spacing: 10px;
}

.task-collection::before {
  content: "";
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  position: absolute;
  left: 0;
  height: var(--barcode-height);
  bottom: var(--barcode-bottom-spacing);
  width: 100%;
}

.tag-row {
  height: 0px;
  width: 100%;
  background: green;
  position: absolute;
  --protocol-tag-height: 20px;
  bottom: calc(
    var(--barcode-bottom-spacing) + var(--barcode-height) + 5px +
      var(--protocol-tag-height)
  );
}

.tag-row::after {
  content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  position: absolute;
  top: 0;
  right: 10px;
  width: 50px;
  height: var(--protocol-tag-height);
}

.task-collection {
  display: grid;
  grid-auto-rows: min-content;
  gap: var(--task-collection-padding);
  position: relative;
  --bottom-space: 60px;
  padding: var(--header-content-spacing) var(--task-collection-padding)
    var(--bottom-space) var(--task-collection-padding);
  font-family: "Rajdhani";
}

.task-collection :is(.task-input, .task-notes, .task-date, .task-priority) {
  font-family: inherit;
}

.task-collection:not(.all-tasks)::after {
  content: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -70%);
  width: 137px;
  height: 159px;
}

.task-collection[data-collection-state="populated"]::after {
  display: none;
}

.page-wrapper.desktop-sidebar .task-collection-wrapper {
  grid-column: 2;
}

.page-wrapper.desktop-sidebar .task-collection {
  padding-left: calc(var(--task-collection-padding) * 2);
}
